$white: #fff;
$red: #cd0540;
$yellow: #FBD311;
$orange: #F18024;
$green_1: #054C35;
$green_2: #027351;
$green_3: #038B62;

$font_path: '../../fonts/';

@import '../../scss/helpers/helpers-all';
@import '../../scss/reset';

// ---------------------------------------------------------------------------------fonts#
@include font_face('LondrinaSolid-Regular', 'LondrinaSolid', $font_path, 400);
@include font_face('LondrinaSolid-Light', 'LondrinaSolid', $font_path, 300);
// ---------------------------------------------------------------------------------fonts##

// // ---------------------------------------------------------------------------------base#
:root {
	--full-screen: calc(var(--vh, 1vh) * 100);

	--gl-indent: 0;
	--content-width: 100%;

	--font_1: 'LondrinaSolid', sans-serif;
	--font-size-html: calc(100vw / 1920 * 10);
	--font-size-body: 1rem;
	--color-text-base: #000;

	--body-background: #fff;
	--accent-color: #4aa3df;
	--input-placeholder-color: var(--color-text-base);
}

html {
	height: 100%;
	font-family: var(--font_1);
	font-weight: 400;
	line-height: 1.33;
	//font-size: var(--font-size-html);
	font-size: 10px;
}

body {
	position: relative;

	height: 100%;

	font-size: var(--font-size-body);
	color: var(--color-text-base);

	-webkit-text-size-adjust: none;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;

	background: var(--body-background);
}

.wrapper {
	position: relative;

	width: 100%;
	display: flex;
	flex-direction: column;
	min-height: 100%;
	height: auto !important;

	overflow-x: hidden;
}

.base {
	width: 100%;
	position: relative;

	flex: 1 0 auto;
}

.section {
	position: relative;
	overflow: hidden;

	width: 100%;
}

.section_in {
	max-width: var(--content-width);
	width: 100%;
	margin: 0 auto;
}

// ---------------------------------------------------------------------------------base##
// ---------------------------------------------------------------------------------styles for components#
// ---------------------------------------------------------------------------------styles for components##

@mixin cover_img {
	display: block;
	width: 100%;
	height: 100%;
	object-fit: cover;
}

//=================================================
.tropicana {
	transition: background-color .6s ease;

	&.tropicana--color_v0_mod {
		background-color: $yellow;
	}

	&.tropicana--color_v1_mod {
		background-color: $red;
	}

	&.tropicana--color_v2_mod {
		background-color: $green_2;
	}
}

.tropicana__wrapper {
	position: relative;
	text-align: center;
}

.tropicana__decor {
	position: absolute;
	z-index: 10;

	&.tropicana__decor--v1_mod {
		top: 0;
		right: 0;

		width: 8.3em;
		height: 13.45em;
		margin: 14.5em -2em 0 0;
	}

	&.tropicana__decor--v2_mod {
		top: 0;
		left: 0;

		width: 3em;
		height: 3em;
		margin: 24.3em 0em 0 10em
	}

	&.tropicana__decor--v3_mod {
		top: 0;
		left: 0;

		width: 21.6em;
		height: 15.8em;
		margin: 22.4em 0em 0 -16.1em;

		filter: blur(1.1em);
	}

	&.tropicana__decor--v4_mod {
		bottom: 0;
		left: 0;

		width: 34.1em;
		height: 24.3em;
		margin: 0 0 -7.2em -26.3em;
	}

	&.tropicana__decor--v5_mod {
		bottom: 0;
		right: 0;

		width: 45em;
		height: 36em;
		margin: 0 -34em -25em 0;

		filter: blur(1.2em);
	}

	&.tropicana__decor--v6_mod {
		bottom: 0;
		right: 0;

		width: 2.7em;
		height: 1.5em;
		margin: 0 6em 13.2em 0;
	}
}

.tropicana__decor_img {
	@include cover_img;
}

//===========Product===============

.tropicana_product {
	min-height: var(--full-screen);
	padding: 7.6em 0 3em;
	transition: background-color .3s ease;
}

.tropicana_product__in {

}

.tropicana_product__title {
	text-transform: uppercase;
	line-height: 1;

	&.tropicana_product__title--size_v1_mod {
		font-size: 10em;
		color: $green_2;
	}

	&.tropicana_product__title--size_v2_mod {
		font-size: 8em;
		color: $yellow;
	}

	&.tropicana_product__title--size_v3_mod {
		font-size: 7em;
		color: $white;
	}

	&:not(:last-child) {
		margin-bottom: calc(4em / 10);
	}
}

.tropicana_product__pic {
	display: flex;
	justify-content: center;
	align-items: center;
}

.tropicana_product__pic_in {
	position: relative;
}

.tropicana_product__img {
	display: block;

	&.tropicana_product__img--v1_mod {
		width: 22.3em;
		height: 44.4em;
	}

	&.tropicana_product__img--v2_mod {
		width: 25em;
		height: 45.6em;
	}

	&.tropicana_product__img--v3_mod {
		width: 22.5em;
		height: 44.7em;
	}
}

.tropicana_product__img_in {
	@include cover_img;
}

.tropicana_product__decor_top {
	z-index: -1;
	position: absolute;
	top: 0;
	left: 0;

	&.tropicana_product__decor_top--v1_mod {
		width: 10.5em;
		height: 8.6em;
		margin: 1.2em 0 0 -2.2em;
	}

	&.tropicana_product__decor_top--v2_mod {
		width: 8.2em;
		height: 8.2em;
		margin: 0.9em 0 0 1.6em;
	}

	&.tropicana_product__decor_top--v3_mod {
		width: 14.4em;
		height: 12em;
		margin: -1.7em 0 0 -1.6em;
	}
}

.tropicana_product__decor_bottom {
	position: absolute;
	bottom: 0;
	right: 0;

	&.tropicana_product__decor_bottom--v1_mod {
		width: 11.8em;
		height: 11.8em;
		margin: 0 -3.9em 2.8em 0;
	}

	&.tropicana_product__decor_bottom--v2_mod {
		width: 10.5em;
		height: 10.5em;
		margin: 0 -1.7em 2.4em 0;
	}

	&.tropicana_product__decor_bottom--v3_mod {
		width: 11.3em;
		height: 9em;
		margin: 0 -3em 1em 0;
	}
}

.tropicana_product__decor_img {
	@include cover_img;
}

.swiper-pagination-bullet {
	.tropicana & {
		width: 1.4em;
		height: 1.4em;
		margin: 0 1.2em !important;

		background: $white;
		opacity: .59;
	}

	&.swiper-pagination-bullet-active {
		.tropicana & {
			opacity: 1;
		}
	}
}

.slider_bullets.swiper-pagination-bullets {
	z-index: 20;
	position: absolute;
	bottom: 2.5em !important;
}

//===============Fraction================

.slider_fraction {
	z-index: 20;
	position: absolute;
	top: 50%;
	right: 2em;

	display: flex;
	align-items: baseline;
	margin-top: -1.4em;

	color: $white;
}

.slider_fraction__current {
	font-size: 2.4em;
	font-weight: 300;
}

.slider_fraction__sepparator {
	font-size: 2.4em;
	font-weight: 300;
	margin: 0 calc(0.7em / 2.4);
}

.slider_fraction__total {
	font-size: 1.4em;
	font-weight: 300;
}

